<template>
  <table class="table">
    <thead class="thead-dark">
      <tr>
        <th scope="col">#</th>
        <th scope="col">Name</th>
        <th scope="col">Category</th>
        <th scope="col">#</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="restaurant in restaurants" :key="restaurant.id">
        <th scope="row">{{restaurant.id}}</th>
        <td>{{restaurant.name}}</td>
        <td>{{restaurant.Category?restaurant.Category.name : '未分類'}}</td>
        <td>
          <button type="button" class="btn btn-link">
            <router-link :to="{name:'admin-restaurant',params:{id:restaurant.id}}">Show</router-link>
          </button>
          <button type="button" class="btn btn-link">
            <router-link :to="{name:'admin-restaurant-edit',params:{id:restaurant.id}}">Edit</router-link>
          </button>

          <button
            type="button"
            @click.prevent.stop="deleteRestaurant(restaurant.id)"
            class="btn btn-link"
          >Delete</button>
        </td>
      </tr>
    </tbody>
  </table>
</template>
<script>
import adminAPI from "../apis/admin";
import { Toast } from "../utils/helpers";
export default {
  data() {
    return {
      restaurants: []
    };
  },
  created() {
    this.fetchRestaurants();
  },
  methods: {
    async fetchRestaurants() {
      try {
        const { data } = await adminAPI.restaurants.get();
        if (data.status === "error") {
          throw new Error(data.message);
        }
        this.restaurants = data.restaurants;
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: error.message || "無法顯示餐廳列表,請稍後再試"
        });
      }
    },
    async deleteRestaurant(id) {
      const { data } = await adminAPI.restaurants.delete({ restaurantId: id });
      try {
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        // 觸發父層事件 - $emit( '事件名稱' , 傳遞的資料 )
        this.restaurants = this.restaurants.filter(
          restaurant => restaurant.id !== id
        );
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法刪除餐廳,請稍後再試"
        });
      }
    }
  }
};
</script>